.tttpre {
  background-color: rgb(213, 213, 213);
  height: fit-content;
  width: 100%;
  padding: 10px;
  border-radius: 10px;
  display: inline-grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 1rem;
  place-items: center;
  margin-bottom: 38.5px;
  transition: all ease-in-out 0.17s;
}
.tttpre::-webkit-scrollbar {
