.newsBigBox {
  background-color: black;
}

.newsBox {
  display: grid;
  flex-direction: column;
}
.newsBox__news__text {
  background-color: aquamarine;
}
.newsBox__news__heading {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
