.home {
  margin: 0 2%;
  margin-top: 2.5rem;
  font-family: Poppins;
}
.home__time {
  position: absolute;
  right: 0;
  margin-right: 3rem;
  display: inline;
  text-align: right;
}
.home__time h2 {
  margin-right: 100px;
}

.hero {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #3250bf;
}
.hero__notification {
  position: absolute;
  display: inline-block;
  max-width: 300px;
  padding: 20px 15px;
  border-radius: 4px;
  background-color: var(--gray-light-2);
  color: var(--font-color);
  box-shadow: 0 1px 10px rgb(0, 0, 0);
  transform: translateY(30px);
  opacity: 0;
  visibility: hidden;
  animation: fade 3.65s linear forwards;
  right: 10px;
  z-index: 99 !important;
  left: 7px;
}
@keyframes fade {
  5% {
    opacity: 1;
    visibility: visible;
    transform: translateY(0px);
    position: absolute;
  }
  90% {
    opacity: 1;
    transform: translateY(0px);
  }
  95% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateY(-30px);
    visibility: hidden;
  }
}
.hero__progress {
  position: absolute;
  left: 5px;
  bottom: 5px;
  width: 0;
  height: 3px;
  background-image: linear-gradient(to right, #529bdb, #3250bf);
  border-radius: 4px;
  animation: progress 3s 0.25s linear forwards;
  position: -webkit-sticky; /* Safari */
  position: sticky;
  top: 0;
}
@keyframes progress {
  to {
    width: calc(100% - 10px);
  }
}

