.developers {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5rem;

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    place-items: center;
    grid-gap: 2rem;
    max-width: 95vw;

    @include respond(tab-port) {
      grid-template-columns: 1fr;
    }
  }

  &__profile {
    justify-content: center;
    text-align: center;
    background-color: #e0e0e0;
    //  background-color: transparent;
    width: 100%;
    padding: 3rem;
    border-radius: 1.1rem;
    box-shadow: 0px 0px 22px rgba(20, 20, 20, 0.318);
    transition: all 0.2s ease-in-out;
    h2 {
      transition: all ease-in-out 1s;
      font-size: 2.5rem;
    }
    h3 {
      font-size: 1.5rem;
      transition: all ease-in-out 1s;
    }
    img {
      justify-content: center;
      width: 30rem;
      // width: fit-content;
      max-height: 20rem;
      border-radius: 10px;
      margin-bottom: 15px;
      background-size: 30rem;
      background-repeat: no-repeat;
      background-position: center;
      object-fit: cover;
      object-position: 0% 20%;
      overflow: hidden;
      transition: all ease-in-out 1s;
    }
    :hover {
      // background-color: #000000;
      border-radius: 10px;
      // transform: scale(1.03);
      // visibility: visible;
    }
    &:hover {
      transform: scale(1.03);
    }
    &:active {
      transform: scale(0.92);
    }
  }

  &__social {
    width: fit-content;
    display: inline-block;
    flex-direction: row;
    // flex-wrap: wrap;
    position: relative;
    text-decoration: none;
    margin-bottom: -100px;
    // gap: 100px;
    // right: -30rem;
    bottom: 0px;
    background-color: transparent;
    font-size: 35px;
    width: 100%;
    transition: all ease-in-out 1s;
    :hover {
      transform: scale(1.03);
      padding: 4px;
      background-color: #c4c4c4;
    }
  }
}
